
ul.logo {

    list-style: none;

    li {
        padding: 1.3rem;
        vertical-align: middle;
    }

    .logo-description {
        @extend .title;
        border-top: 1px solid $grey;
        color: $grey !important;
        font-weight: normal;
    }
    
    .logo-image {
        @extend .title;
        margin-bottom: 0;
        color: $blue !important;
    }
}

.logo-container {
    .hero & {
        display: table;
        margin: 0 auto;
    }
}