.goal-outline {

    .desc, .graphic {
        padding: 2rem;
        min-height: 250px;
    }

    .graphic {
        background-color: $grey-dark;
        padding: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        img {
            max-width: 75%;
        }
    }

    .desc {
        background-color: white;
        position: relative;
        padding: 2rem;
        
        .title {
            padding: 2rem 0 0 2rem;
        }

        .content {
            padding: 0 2rem;

            &:last-child {
                padding-bottom: 2rem;
            }
        }
    }

    // Caret
    .desc::before {
        content: '';
        width: 0;   
        height: 0; 
        border-top: 30px solid transparent;
        border-bottom: 30px solid transparent;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
    }

    &.orientation-2 .desc::before {
        border-left: 30px solid $grey-dark;
        left: 0;
    }

    &.orientation-1 .desc::before {
        border-right: 30px solid $grey-dark;
        right: 0;
    }
}



.goal-outline-container {
    @include clearfix;
    background-color: $grey-light;
}