.navbar-transparent {
      @extend .is-shadowless;
      background-color: rgba(255,255,255, 0);
      
      .logo { 
            opacity: 0;
      }
}

.navbar {
      transition-property: all;
      transition-duration: 1s;

      * {
            font-size: 1.2rem;
      }

      .logo {
            transition-property: all;
            transition-duration: 1s;
      }
}