.news-outline {
	@extend .is-unselectable;

	.news-outline-image {
		background-size: cover;
		background-repeat: no-repeat;
	}

	.news-date {
		opacity: 0.7;
		filter: alpha(opacity=70);
	}
}

#news_outline {
	.slider-slide {
		padding: 20px;
	}

	.slider-control-bottomcenter {
		bottom: -25px !important;
	}
}