$team-member-border-color: darken($grey-light, 6%);

.team-outline-container {
	background-color: $grey-light;
}

.team-member {
	background-color: white;
	padding: 1rem;
	border: 1px solid $team-member-border-color;

	.team-member-image {
		max-width: 70%;
		margin: 0 auto;
		margin-top: -50px;
		background-color: white;
		padding: 0.5rem;
		border: 1px solid $team-member-border-color;
	    border-radius: $radius-rounded
	}

	.team-member-name {
		text-transform: uppercase;
		text-align: center;
		margin-top: 1rem;
		font-size: 1.1rem;
		margin-bottom: 0;
	}

	.team-member-title {
		margin-bottom: 1rem;
		color: $grey;
		font-style: italic;
		text-align: center
	}
}
